import React from 'react';
import { withStyles } from '@material-ui/core/styles';

import GridBlock from '../components/grid-block';
import GridColumn from '../components/grid-column';
import GridItem from '../components/grid-item';
import PostExcerpt from '../components/post-excerpt';

import Hero from '../components/hero';

const lrgImage = 'https://github.com/HyperSprite/redux-todo-with-auth/raw/master/web/src/server/public/images/background-large.jpg';
const smlImage = 'https://github.com/HyperSprite/redux-todo-with-auth/raw/master/web/src/server/public/images/background-small.jpg';

const styles = theme => ({
  root: {
    margin: '0 0 100px 0'
  },
  headlineBase: {
    // '@global': theme.typography.global,
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
  },
  headlineStage: {
    margin: 20,
    fontSize: '1.4em',
    fontWeight: 400,
    maxWidth: 600,
    '& p': {
      margin: 20,
    },
  },
});

class Features extends React.Component {
  getPostList() {
    const postList = this.props.data.allMarkdownRemark.edges.filter(
      eF => eF.node.frontmatter.features).map(edge => (
        {
          excerpt: edge.node.excerpt,
          html: edge.node.html.substr(0, edge.node.html.indexOf('<hr>')),
          slug: edge.node.fields.slug,
          linkPath: edge.node.frontmatter.path,
          title: edge.node.frontmatter.title,
          date: edge.node.frontmatter.date,
          cover: edge.node.frontmatter.cover
            ? edge.node.frontmatter.cover.childImageSharp.responsiveSizes
            : null,
        }
    ));
    return postList;
  }
  render() {
    const postList = this.getPostList();
    return (
      <div className={this.props.classes.root}>
        <GridBlock>
          <div className={this.props.classes.headlineBase}>
            <div className={this.props.classes.headlineStage}>
              <h1>Welcome to A Race athlete</h1>
              <p>
                {'Getting started is easy. All you need to do is click the '}
                <strong>{'Connect with STRAVA'}</strong>
                {' button in the upper right corner. You will be redirected to Strava to sign in and they will return you, logged in and ready to go.'}
              </p>
              <p>
                {'Check out the features below.'}
              </p>
            </div>
          </div>
          <GridColumn>
            {postList.map(page => (
              <GridItem key={page.linkPath} page={page} PostType={PostExcerpt} />
            ))}
          </GridColumn>
        </GridBlock>
      </div>
    );
  }
}

export default withStyles(styles, { name: 'StyledFeatures' })(Features);

/* eslint no-undef: "off"*/
export const pageQuery = graphql`
  query FeaturesQuery {
  allMarkdownRemark(
      limit: 2000,
      sort: { fields: [frontmatter___features], order: ASC },
    ) {
      edges {
        node {
          excerpt(pruneLength: 250)
          fields {
            slug
          }
          html
          frontmatter {
            title
            date
            path
            parent
            features
            cover {
              childImageSharp {
                sizes(maxWidth: 2000) {
                  ...GatsbyImageSharpSizes_noBase64
                }
              }
            }
          }
          html
        }
      }
    }
}
`;



