import React from 'react';
import PropTypes from 'prop-types';
import { navigateTo } from 'gatsby-link';
import classNames from 'classnames';
import Img from 'gatsby-image';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';

const propTypes = {

};

const styles = theme => ({
  root: {
    // '@global': theme.typography.global,
    textAlign: 'left',
    color: theme.palette.secondary.dark,
    margin: '10px 0 0 0',
    transition: `all ${(theme.transitions.duration.standard * 0.001).toFixed(2)}s ${theme.transitions.easing.easeInOut}`,
    borderRadius: 2,
    [theme.breakpoints.up('md')]: {
      maxWidth: 412,
      margin: '20px',
    },
  },
  withHover: {
    '&:hover, :active': {
      boxShadow: theme.shadows[20],
      cursor: 'pointer',
    },
    '&:focus': {
      boxShadow: theme.shadows[20],
      outlineColor: theme.palette.primary.dark,
      cursor: 'pointer',
    },
  },
  flexBody: {
    display: 'flex',
    flexWrap: 'wrap',
    alignContent: 'stretch',
    [theme.breakpoints.up('lg')]: {
      justifyContent: 'center',
      flexGrow: 1,
    },
  },
  cover: {
    maxWidth: 640,
    minWidth: 300,
    flexGrow: 2,
    [theme.breakpoints.up('md')]: {
      minWidth: 395,
    },
  },
  excerpt: {
    flexGrow: 1,
    display: 'flex',
    flexWrap: 'wrap',
    padding: theme.spacing.unit * 2,
    [theme.breakpoints.up('md')]: {
      maxWidth: 400,
    },
  },
  title: {
    fontSize: 22,
    margin: '0.5em',
    maxWidth: 500,
    color: theme.palette.primary[900],
    borderBottom: `3px solid ${theme.palette.primary[600]}`,
    paddingBottom: '2px',
    '&:hover': {
      color: theme.palette.primary[600],
      borderBottom: `5px solid ${theme.palette.primary[500]}`,
      paddingBottom: '0',
    },
    [theme.breakpoints.up('md')]: {
      fontSize: 28,
      fontWeight: 700,
    },
  },
  contentBody: {
    display: 'flex',
    flexDirection: 'column',
  },
  contentPushOne: {
    flex: 2,
    display: 'flex',
    justifyContent: 'center',
    flexDirection: 'column',
  },
  contentPushTwo: {
    flex: 1,
    display: 'flex',
    justifyContent: 'center',
    flexDirection: 'column',
  },
  contentHTML: {
    fontSize: 18,
    flexGrow: 2,
    paddingBottom: theme.spacing.unit * 2,
    [theme.breakpoints.up('md')]: {
      fontSize: 20,
    },
  },
  space: {
    flexGrow: 3,
  },
  tags: {
    flexGrow: 1,
  },
});

const PostExcerpt = (props) => {
  const { classes, cover, excerpt, feature, html, linkPath, path, title, tags } = props;
  const link = linkPath ? `/blog${linkPath}` : null;
  const navTo = path || link;
  const ftrSwtch = {
    onClick: null,
    tab: null,
    role: null,
    link: false,
  };

  if (path || linkPath) {
    if (link) {
      ftrSwtch.onClick = () => window.open(navTo, '_blank');
    } else {
      ftrSwtch.onClick = () => navigateTo(navTo);
    }
    ftrSwtch.tab = 1;
    ftrSwtch.role = 'button';
    ftrSwtch.link = true;
  }
  const handleKeyPress = (e) => {
    const key = e.keyCode || e.which;
    if (key === 13) {
      ftrSwtch.onClick();
    }
  }

  return (
    <Card
      className={classNames(classes.root, { [classes.withHover]: ftrSwtch.link })}
      role={ftrSwtch.role}
      tabIndex={ftrSwtch.tab}
      onClick={ftrSwtch.onClick}
      onKeyPress={event => handleKeyPress(event)}
    >
      <div className={classes.flexBody}>
        <div className={classes.cover}>
          {cover ? (
            <Img sizes={cover} alt={title} />
          ) : (
            <Typography
              className={classes.title}
              variant="title"
            >
              {title}
            </Typography>
          )}
        </div>
        <CardContent className={classes.excerpt}>
          <div
            className={classes.contentHTML}
            dangerouslySetInnerHTML={{ __html: html }}
          />
        </CardContent>
      </div>
    </Card>
  );
};

PostExcerpt.propTypes = propTypes;

export default withStyles(styles, { name: 'StyledPostFull' })(PostExcerpt);



